
#app {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.todo-list__router-wrapper {
  position: fixed;
  top: 120px;
  left: 0;
  right: 0;
  bottom: 0;
}

.todo-list__title, .todo-list__motto {
  text-align: center;
}
html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html, body {
  height: 100%;
  color: #696969;
}
body {
  background-color: #f0f0f6;
}
::-webkit-input-placeholder {
  color: #959ba2;
}
:-ms-input-placeholder {
  color: #959ba2;
}
::-ms-input-placeholder {
  color: #959ba2;
}
::placeholder {
  color: #959ba2;
}
.slip-enter-active,
.slip-leave-active,
.slip-reverse-enter-active,
.slip-reverse-leave-active {
  -webkit-transition: .3s;
  transition: .3s;
}
.slip-enter,
.slip-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.slip-reverse-enter,
.slip-reverse-leave-to {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.fadeIn-enter-active,
.fadeIn-leave-active {
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
.fadeIn-enter {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
  opacity: 0;
}
.fadeIn-leave-to {
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
  opacity: 0;
}
.todo-list__title {
  color: #1AAD19;
  font-weight: normal;
  letter-spacing: 2px;
  padding-top: 5vh;
}
.todo-list__motto {
  color: #959ba2;
}

